import { useEffect, useState, useRef } from 'react'
import './index.scss'
import { getUser } from '@/utils/storageUtils'
import logo from '@/assets/logo.png'
import pressure from '@/assets/pressure.png'
import normal from '@/assets/normal.png'
import relax from '@/assets/relax.png'
import { Card, Button, message, Divider, Alert } from 'antd'
import {
  PlayCircleOutlined,
  SwapOutlined,
  MonitorOutlined,
  UndoOutlined,
  CopyrightOutlined,
  VideoCameraOutlined,
  SmileOutlined,
} from '@ant-design/icons'
import axios from 'axios'
import Marquee from 'react-fast-marquee'

const Home = () => {
  var tel = getUser('user_key')

  const [showElem, setshowElem] = useState(false)
  const [showElem2, setshowElem2] = useState(false)
  const [showElem3, setshowElem3] = useState(false)
  const [showElem4, setshowElem4] = useState(false)
  const [showElem5, setshowElem5] = useState(false)
  const [showElem6, setshowElem6] = useState('none')
  const [showElem7, setshowElem7] = useState('none')
  const [showElem8, setshowElem8] = useState('none')

  // Timer
  const [title, setTitle] = useState('開始錄製')
  const [timeLeft, setTimeLeft] = useState(5)
  const [isCounting, setIsCounting] = useState(false)
  const intervalRef = useRef(null)
  const inputRef = useRef("")
  const [alterType, setAlterType] = useState("warning")

  const timeCounter = (time) => {
    return time.toString().padStart(2, "0")
  }

  const cameraVideoRef = useRef(null)

  const [mediaStream, setmediaStream] = useState(null)

  const [Stream, setStream] = useState(false)

  const [videoBlob, setvideoBlob] = useState(null)

  const [mediaRecorder, SetmediaRecorder] = useState(null)

  const [Type, setType] = useState(true)

  const [Phone, setPhone] = useState()
  const [Height, setHeight] = useState()
  const [Weight, setWeight] = useState()
  const [Dt, setDt] = useState()

  const [HR, setHR] = useState(0)
  const [SBP, setSBP] = useState(0)
  const [DBP, setDBP] = useState(0)
  const [SpO2, setSpO2] = useState(0)
  const [SDNN, setSDNN] = useState(0)
  const [RMSSD, setRMSSD] = useState(0)
  const [Result, setResult] = useState(0)
  const [Emotext, setEmotext] = useState('壓力正常')
  const [Emo, setEmo] = useState(normal)

  useEffect(() => {
    getUserInfo()
    setshowElem('none')
    setshowElem3('none')
    getUserMediaStream()
  }, [])

  function getUserMediaStream (constraints, cb) {

    let video = document.querySelector('#viodes')

    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {

      navigator.mediaDevices.getUserMedia({ audio: true, video: { facingMode: Type ? "user" : "environment" } }).then((stream) => {

        if ("srcObject" in video) {

          video.srcObject = stream

          video.play()

          // onluzhis(stream)

          setStream(stream)

        } else {

          video.src = window.URL && window.URL.createObjectURL(stream) || stream

          console.log('第二種')

        }

      })

    } else if (navigator.webkitGetUserMedia || navigator.mozGetUserMedia) {

      // webkit内核浏览器

      if (navigator.mediaDevices === undefined) {

        navigator.mediaDevices = {}

      }

      console.log('我是webkit内核')

    }

  }

  const onClickzp = () => {

    const viodes = document.getElementById('viodes')

    const canvas = document.getElementById('canvas')  // canvas元素

    const context = canvas.getContext('2d')

    context.drawImage(viodes, 0, 0, 420, 300) // 将video在canvas上绘制出来

    canvas.toBlob(blob => {   // 将canvas转换为blob

      console.log(blob)

    })

  }

  const updateVideo = (ctx, video, rate) => {

    ctx.drawImage(video, 0, 0, 420, 300 * rate)// 使用视频帧(当前帧)绘制canvas

    video.requestVideoFrameCallback(updateVideo)

  }

  function onluzhis () {
    setAlterType("warning")
    setTitle('開始錄製')

    setshowElem('none')

    setshowElem2('none')

    setshowElem5()

    setshowElem4('none')

    setshowElem3()

    setshowElem6('none')

    setshowElem7('none')

    setshowElem8()

    let options = {

      audioBitsPerSecond: 128000,

      videoBitsPerSecond: 2500000,

      mimeType: "video/webm;codecs=vp8,opus"//webm类型一定要加codecs=vp8,opus，否则会导致录制时候时而可以用时而不能用

    }

    let recorder = new MediaRecorder(Stream, options)

    setTimeout(function () {
      console.log('開始錄製')

      SetmediaRecorder(recorder)

      recorder.ondataavailable = function (evt) {

        window.bobals = new Blob([evt.data], { type: evt.data.type })

        // setvideoBlob(bobal)

      }
      recorder.start()

    }, 0)

    setTimeout(function () { startTimer() }, 0)

    setTimeout(function () { recorder.stop() }, 7000)

    setTimeout(function (e) {

      console.log("錄影上傳", window.bobals)

      let blob = new Blob([window.bobals])

      let downloadElement = document.createElement("a")

      // 創建下載的鏈結
      let href = window.URL.createObjectURL(blob)

      downloadElement.href = href

      // 下載文件名
      /*downloadElement.download = formatDateTime(new Date()) + '.webm' //下载后文件名

      document.body.appendChild(downloadElement)

      downloadElement.click() //点击下载
      */

      getBase64(blob)
      // 下载完成移除元素
      document.body.removeChild(downloadElement)

      // 釋放掉 blob 對象
      window.URL.revokeObjectURL(href)

    }, 8000)

    setTimeout(function () {
      setshowElem()

      setshowElem2('none')

      setshowElem5('none')

      setshowElem8('none')

      setshowElem6()

      const viodes = document.getElementById('viodes')

      const canvas = document.getElementById('canvas')  // canvas元素

      const context = canvas.getContext('2d')

      context.drawImage(viodes, 0, 0, 270, 300) // 将video在canvas上绘制出来

      canvas.toBlob(blob => {   // 将canvas转换为blob

        console.log(blob)

      })
    }, 9000)
  }

  function onrestart () {
    setTitle('錄前調整')
    setshowElem2()
    setshowElem3('none')
    setshowElem4()
    setshowElem5()
    setshowElem7('none')
  }

  const endsTartOnload = () => {

    mediaRecorder.stop()

    setTimeout(function () {

      console.log("錄影上傳", window.bobals)

      let blob = new Blob([window.bobals])

      let downloadElement = document.createElement("a")

      // 創建下載的鏈結
      let href = window.URL.createObjectURL(blob)

      downloadElement.href = href

      // 下載文件名
      downloadElement.download = formatDateTime(new Date()) + '.webm' //下载后文件名

      document.body.appendChild(downloadElement)

      downloadElement.click() //点击下载

      // 下载完成移除元素
      document.body.removeChild(downloadElement)

      // 釋放掉 blob 對象
      window.URL.revokeObjectURL(href)

    }, 1000)

  }

  let formatDateTime = function (date) {

    var y = date.getFullYear()

    var m = date.getMonth() + 1//注意这个“+1”

    m = m < 10 ? ('0' + m) : m

    var d = date.getDate()

    d = d < 10 ? ('0' + d) : d

    var h = date.getHours()

    h = h < 10 ? ('0' + h) : h

    var minute = date.getMinutes()

    minute = minute < 10 ? ('0' + minute) : minute

    var second = date.getSeconds()

    second = second < 10 ? ('0' + second) : second

    return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second

  }

  const qiehuan = () => {

    setType(!Type)

    stopCapture()

  }

  const stopCapture = () => {

    var video = document.getElementById('viodes')

    if (!video.srcObject) return

    let stream = video.srcObject

    let tracks = stream.getTracks()

    tracks.forEach(track => {

      track.stop()

      getUserMediaStream()

    })

  }

  const closeMedia = () => {
    getUserInfo2()
    /*const video = document.getElementById('viodes')
    if (!video.srcObject) return
    let stream = video.srcObject
    if ('getTracks' in stream) {
      const tracks = stream.getTracks()
      tracks.forEach(track => {
        track.stop()
      })
    }*/
  }

  function startTimer () {
    if (intervalRef.current !== null) return
    setIsCounting(true)
    inputRef.current = ""
    intervalRef.current = setInterval(() => {
      setTimeLeft((timeLeft) => {
        setTitle(`錄影 ${timeLeft} 秒後結束`)
        if (timeLeft >= 1) return timeLeft - 1
        // reset the timer
        resetTimer()
        return 0
      })
    }, 1000)
  }

  /*function stopTimer () {
    if (intervalRef.current === null) return
    setTitle(`Keep the countdown!`)
    setIsCounting(false)
    clearInterval(intervalRef.current)
    intervalRef.current = null
  }*/

  function resetTimer () {
    clearInterval(intervalRef.current)
    setAlterType("success")
    setTitle(`完成錄影`)
    setTimeLeft(5)
    setIsCounting(false)
    intervalRef.current = null
  }

  /*function inputTime (e) {
    inputRef.current = e.target.value
    setTimeLeft(e.target.value)
  }*/

  const minutes = timeCounter(Math.floor(timeLeft / 60))
  const seconds = timeCounter(timeLeft - minutes * 60)

  // 影片轉 base64
  const onChange = (e) => {
    const files = e.target.files
    const file = files[0]
    getBase64(file)
  }
  const onLoad = (fileString) => {
    var str = fileString
    var final = str.substr(37, str.lenth)
    setDt(final)
    console.log(final)
  }
  const getBase64 = (file) => {
    let reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => {
      onLoad(reader.result)
    }
  }

  const getUserInfo = async () => {
    // 調用接口獲取數據
    const res = await axios.get(`https://192.168.11.118:7000/user/profile/${tel}`)
    var userInfo = res.data
    var ph = userInfo.data[0].Tel
    var hei = userInfo.data[0].height
    var wei = userInfo.data[0].weight
    setPhone(ph)
    setHeight(hei)
    setWeight(wei)
  }

  const postData = {
    "uuid": Phone,
    "height": Height,
    "weight": Weight,
    "content": Dt
  }

  const getUserInfo2 = async () => {
    setAlterType("warning")
    setTitle(`資料處理中`)
    message.loading('上傳中', 0)
    // 調用接口獲取數據
    var response = await fetch('/api1/api/v1.0/measure/', {
      method: 'POST',
      headers: {
        "Content-Type": 'application/json',
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Credentials": "true",
        "Access-Control-Allow-Headers": "Content-Type,Access-Control-Allow-Headers,Content-Length,Accept,Authorization,X-Requested-With",
        "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS"
      },
      body: JSON.stringify(postData)
    })
    //response.status gives the response code
    //response.statusText gives the message for response status
    if (response.status === 200) {
      message.destroy()
      var data = await response.json()
      var userInfo2 = JSON.parse(data)
      //process data
      if (Array.isArray(userInfo2) === false && userInfo2.hbr.length < 1) {
        setAlterType("error")
        message.error('回傳出現錯誤')
        setTitle(`請重新錄製!!`)
      } else {
        var hr = userInfo2.hbr[userInfo2.hbr.length - 1]
        var sbp = userInfo2.ica[userInfo2.hbr.length - 1].SBP
        var dbp = userInfo2.ica[userInfo2.hbr.length - 1].DBP
        var sp = userInfo2.sp[userInfo2.hbr.length - 1]
        var rmssd = userInfo2.hrv[userInfo2.hbr.length - 1].RMSSD
        var sdnn = userInfo2.hrv[userInfo2.hbr.length - 1].SDNN
        var res = sdnn / rmssd
        console.log(res)
        setAlterType("success")
        setTitle('檢測結果')
        setshowElem('none')
        setshowElem3()
        setshowElem6('none')
        message.success('計算完成')
        setHR(hr)
        setSBP(sbp)
        setDBP(dbp)
        setSpO2(sp)
        setRMSSD(rmssd)
        setSDNN(sdnn)
        setResult(res)
        if (res > 2.8) {
          setEmotext('壓力山大')
          setEmo(pressure)
        }
        else if (res < 1.2) {
          setEmotext('心情放鬆')
          setEmo(relax)
        }
        else {
          setEmotext('心平氣和')
          setEmo(normal)
        }
        setshowElem7()
      }
    }
  }

  return (

    <div className='webrtc'>

      <div className='Alert' type="primary" style={{ marginBottom: 10, display: showElem8 }}>
        <Alert
          type="info"
          banner
          message={
            <Marquee pauseOnHover gradient={false}>
              影片錄製中~請微笑 <SmileOutlined />
            </Marquee>
          }
          style={{ color: '#2050c0', fontSize: 18, fontWeight: 'bold', height: 50, width: 230, opacity: 0.7 }}
        />
      </div>

      <div className='buttons' style={{ marginBottom: 20 }}>

        <Button onClick={onluzhis} icon={<VideoCameraOutlined />} style={{ backgroundColor: ' #fe9003', color: 'white', height: 50, width: 370, fontSize: 18, marginRight: 10, display: showElem2 }}>錄製臉部特徵</Button>

        <Button onClick={onluzhis} icon={<UndoOutlined />} style={{ backgroundColor: ' #808080', color: 'white', height: 50, width: 250, fontSize: 18, display: showElem, marginRight: 10 }}>重新錄製</Button>

        <Button onClick={qiehuan} icon={<SwapOutlined />} style={{ backgroundColor: ' #02a65a', color: 'white', height: 50, width: 100, fontSize: 18, display: showElem2 }}>切換</Button>

        <Button onClick={closeMedia} icon={<MonitorOutlined />} style={{ backgroundColor: ' #ff0101', color: 'white', height: 50, width: 250, fontSize: 18, display: showElem }}>開始檢測</Button>

      </div>

      <div className='vedio'>

        <video
          id='viodes'
          style={{
            width: '420px',
            height: '300px',
            display: showElem5
          }}
        />

        <canvas
          id="canvas"
          width={270}
          height={300}
          style={{ width: 270, height: 300, display: showElem6 }}
        />
      </div>

      <div className='tops' style={{ marginTop: 30, marginBottom: 30 }}>
        <Alert message={<div style={{ color: '#2050c0', fontSize: 20 }}>當前鏡頭：{Type ? '前置' : '後置'}</div>} type="info" style={{ display: showElem4 }} />
        <Alert message={<div style={{ color: ' #e90003', fontSize: 20 }}>{title}</div>} type={alterType} showIcon style={{ display: showElem3 }} />
      </div>



      <div className="cards" style={{ marginBottom: 20 }}>

        <Card title="身高(Height)" bordered={false} size='small' headStyle={{ background: "#dedede" }} bodyStyle={{ padding: 15 }} style={{ color: '#026697', textAlign: "center", fontSize: 20, fontWeight: 'bold', width: 300, marginRight: 30 }}>
          <p>{Height} cm</p>
        </Card>

        <Card title="體重(Weight)" bordered={false} size='small' headStyle={{ background: "#dedede" }} bodyStyle={{ padding: 15 }} style={{ color: '#026697', textAlign: "center", fontSize: 20, fontWeight: 'bold', width: 300 }}>
          <p>{Weight} kg</p>
        </Card>

      </div>

      <div className="cards" style={{ marginBottom: 20, display: showElem7 }}>

        <Card title="心率(HR)" bordered={false} size='small' headStyle={{ background: "#dedede" }} bodyStyle={{ padding: 15 }} style={{ className: 'cardstyle', color: '#026697', textAlign: "center", fontSize: 20, fontWeight: 'bold', width: '300px', marginRight: 30 }}>
          <p>{HR} bpm</p>
        </Card>

        <Card title="血壓(BP)" bordered={false} size='small' headStyle={{ background: "#dedede" }} bodyStyle={{ padding: 0 }} style={{ width: '300px', textAlign: "center", color: '#026697', fontSize: 18, fontWeight: 'bold' }}>
          <p> SBP: {SBP}mmHg</p>
          <p> DBP: {DBP}mmHg</p>
        </Card>

      </div>

      <div className="cards" style={{ marginBottom: 20, display: showElem7 }}>

        <Card title="血氧(SpO2)" bordered={false} size='small' headStyle={{ background: "#dedede" }} bodyStyle={{ padding: 15 }} style={{ color: '#026697', textAlign: "center", fontSize: 20, fontWeight: 'bold', width: 630 }}>
          <p> {SpO2}%</p>
        </Card>

      </div>

      <div className="cards" style={{ marginBottom: 280, display: showElem7 }} >

        <Card title="壓力指數(Stress)" bordered={false} headStyle={{ background: "#dedede" }} bodyStyle={{ padding: 5 }} style={{ color: '#026697', textAlign: "center", fontSize: 18, fontWeight: 'bold', width: 630, height: 380 }}>
          <p>{Emotext}</p>
          <hr style={{ borderTopWidth: 3, borderTopColor: "#dedede" }}></hr>
          <img
            className='login-logo'
            alt=""
            src={Emo}
          />
          <hr style={{ borderTopWidth: 3, borderTopColor: "#dedede" }}></hr>
          <p> HRV  SDNN : {SDNN}  RMSSD : {RMSSD}</p>
        </Card>

      </div>

      <div className='buttons'>
        <Button onClick={onrestart} icon={<MonitorOutlined />} style={{ backgroundColor: '#70cce1', color: 'white', height: 50, width: 500, fontSize: 18, fontWeight: 'bold', display: showElem7 }}>重新檢測</Button>
      </div>

      <Divider className='dash' style={{ color: '#4cb804', fontWeight: 'bold', fontSize: 20, borderColor: 'white' }}> <CopyrightOutlined />{new Date().getFullYear()}-遠漢科技</Divider>
      <p className='content'>此為輔助檢測用途，數據僅供參考</p>
    </div>

  )

}

export default Home
